import {useCallback, useMemo} from 'react';
import { IWillFateGraphProps } from './WillFate.contract';
import ChartGraph from '../ChartGraph/ChartGraph';
import {ChartJSOrUndefined} from "react-chartjs-2/dist/types";

const WillFateGraph = ({
  willChartNumbers,
  fateChartNumbers,
}: IWillFateGraphProps) => {
  const labels = ['0', '12', '24', '36', '48', '60', '72'];

  const fateArray = useMemo<number[]>(() => {
    const res = fateChartNumbers.split('').map((val) => parseInt(val, 10));

    return res;
  }, [fateChartNumbers]);

  const willArray = useMemo<number[]>(() => {
    const res = willChartNumbers.split('').map((val) => parseInt(val, 10));

    return res;
  }, [willChartNumbers]);

  const canvasDraw = useCallback((ctx: ChartJSOrUndefined) => {

  }, [])

  return (
    <ChartGraph
      defaultColors={['#a4bae1', '#daa681']}
      data={[fateArray, willArray]}
      labels={labels}
      legends={[' ', ' ']}
      xScaleTitle={' '}
      imageName={'   '}
      displayNumbers={false}
      canvasDraw={canvasDraw}
    />
  );
};

export default WillFateGraph;
