export const calculateYearShift = (
    birthYear: number,
    startYear: number,
    len: number
) => {
    let acc = birthYear;

    while (acc < startYear) {
        acc += len;
    }

    return len - (acc - startYear);
};

export const makeRepeated = (arr: number[], repeats: number) =>
    Array.from({ length: repeats }, () => arr).flat();
